import React from 'react';
import {ContactSection, ContactTitle, Span, ContactForm, FormInput, InputText, InputEmail, InputExp, TextArea, InputSubmit} from './style.js';
import Footer from "./../Footer/index.js";
const Contact = () => {
    return (
        <React.Fragment>
            <ContactSection class="drop">
                <div class="container">
                    <ContactTitle class="drop-title"><Span>Drop </Span>Me A line</ContactTitle>
                    <ContactForm action="">
                        <FormInput class="form-input">
                            <InputText type="text" placeholder="Your Name"/>
                            <InputEmail type="email" placeholder="Your Email"/>
                        </FormInput>
                        <InputExp type="text" class="sub" placeholder="Your Subject"/>
                        <TextArea cols="30" rows="10" placeholder="Your Message"></TextArea>
                        <InputSubmit type="submit" value="Send Message"/>
                    </ContactForm>
                </div>
            </ContactSection>
            <Footer/>
        </React.Fragment>
    )
}

export default Contact;